.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}
.intro-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}
.mask::after{
    content: "";
    width:100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    opacity:0.8;
}
.hero{
height: 100%;
width: 100%;
}
.hero .content{
    position: absolute;
    transform: translate(-50%,-50%);
    top:45%;
    left: 40%;
    text-align: center;
    text-align: start;
   
}
.content>h1+h1+br+h1{
    color: rgb(221, 206, 206);
}
.content>h1+h1+br+h1+p{
    font-size: 22px;
    margin-top: 15px;
    margin-bottom: 15px;
    line-height: 30px;
}